import axios from 'axios';
export default {
  name: 'RideDetails',
  props: ['id', 'original', 'final', 'date', 'time'],
  data() {
    return {
      editing: false,
      editOriginal: this.original,
      editFinal: this.final,
      editDate: this.date,
      editTime: this.time,
      items: []
    };
  },
  methods: {
    backToList() {
      this.$router.push({
        name: 'MainContent'
      });
    },
    editRide() {
      this.editing = true;
    },
    saveRide() {
      const baseURL = window.location.origin + "/index.php/apps/rides";
      const data = {
        id: this.$props.id,
        original: this.editOriginal,
        final: this.editFinal,
        date: this.editDate,
        time: this.editTime
      };
      axios.post(`${baseURL}/api/0.1/edit`, data).then(response => {
        console.log(response.data);
      }).catch(error => {
        console.error(error);
      });
      this.$router.push({
        name: 'MainContent'
      });
    },
    deleteRide() {
      const baseURL = window.location.origin + "/index.php/apps/rides";
      const id = this.$props.id;
      axios.post(`${baseURL}/api/0.1/delete`, id).then(response => {
        console.log(response.data);
      }).catch(error => {
        console.error(error);
      });
      this.$router.push({
        name: 'MainContent'
      });
    }
  }
};