import axios from 'axios';
import NcListItem from '@nextcloud/vue/dist/Components/NcListItem.js';
import NcActionButton from '@nextcloud/vue/dist/Components/NcActionButton.js';
import NcActions from '@nextcloud/vue/dist/Components/NcActions.js';
import Delete from 'vue-material-design-icons/Delete';
import Pencil from 'vue-material-design-icons/Pencil';
import { generateUrl } from "@nextcloud/router";
export default {
  name: "FirstPage",
  components: {
    NcListItem,
    NcActionButton,
    NcActions,
    Delete,
    Pencil
  },
  data() {
    return {
      jsonData: [],
      items: [],
      jsonResponse: {}
    };
  },
  mounted() {
    axios({
      method: 'GET',
      url: 'api/0.1/get',
      headers: {
        'Content-Type': 'application/json',
        'Accept-Encoding': 'application/json'
      }
    }).then(response => {
      this.jsonData = response.data;
      const startingBracket = this.jsonData.indexOf("[");
      const closingBracket = this.jsonData.indexOf("]");
      const respondeString = this.jsonData.substring(startingBracket + 1, closingBracket);
      const jsonResponse = JSON.parse(`[${respondeString}]`);
      console.log(jsonResponse);
      this.jsonResponse = jsonResponse;
    }).catch(error => {
      console.error(error);
    });
  },
  methods: {
    fetchData() {},
    editItem() {},
    deleteItem() {},
    cleanResponse() {}
  }
};